import swal from 'sweetalert';

const successFaces = [
  '(  )',
  '()',
  '()',
  '()',
  '()',
  '(`)/',
  ' ^_^oo^_^ ',
  '(_)',
  '()',
  '()',
  '',
  '()',
  ' ( )',
  '() ',
  '(-).*',
];

export const error = text =>
  swal({
    title: 'Ooops!!',
    text,
    icon: 'error',
    button: 'Ok',
  });

export const success = title =>
  swal({
    title,
    text: successFaces[Math.floor(Math.random() * successFaces.length)],
    icon: 'success',
    timer: 1500,
    buttons: false,
  });



