import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withFormik, Form, Field } from 'formik';

import {
  CardFormInfo,
  MoveButton,
  BackButton,
  DeleteButton,
  SaveButton,
  StyledCheckbox,
  StyledRadio,
} from './styles';
import OptionsInput from '../OptionsInput';

class QuestionForm extends Component {
  addOption = (value) => {
    const { values, setValues } = this.props;
    setValues({
      ...values,
      options: [...values.options, value],
    });
  };

  updateOptionsArray = (newOptionsArray) => {
    const { values, setValues } = this.props;
    setValues({ ...values, options: newOptionsArray });
  };

  removeOption = (i) => {
    const { values, setValues } = this.props;
    setValues({
      ...values,
      options: [
        ...values.options.slice(0, i),
        ...values.options.slice(i + 1, values.options.length),
      ],
    });
  };

  render() {
    const { mode, onModeChange, values } = this.props;

    return (
      <Form>
        <CardFormInfo className="row">
          <div className="col-xl">
            <div>
              <small>Pergunta:</small>
              <Field
                name="questionText"
                component="textarea"
                rows="2"
                placeholder="Qual a sua pergunta?"
                className="col-xl-12 question"
              />
            </div>
            <div>
              <small>Dica:</small>
              <Field
                name="tip"
                component="textarea"
                rows="4"
                placeholder="Escreva algo para ajudar o usurio a responder essa pergunta (opcional)"
                className="col-xl-12"
              />
            </div>
            <div>
              <small>Motivo:</small>
              <Field
                name="reason"
                component="textarea"
                rows="4"
                placeholder="Explique para o usurio por que essa pergunta  importante (opcional)"
                className="col-xl-12"
              />
            </div>
            <div>
              <small>Visibilidade:</small>
              <br />
              <label htmlFor="visible">
                <Field id="visible" name="visible" type="checkbox" checked={values.visible} />{' '}
                <StyledCheckbox checked={values.visible}>
                  Visvel para o usurio:{' '}
                  <span className="checkstatus">{values.visible ? 'SIM' : 'NO'}</span>
                </StyledCheckbox>
              </label>
            </div>
            <div className="row">
              <small className="col-xl-12">Tipo de resposta:</small>
              <br />
              <div className="col-xl-6">
                <label htmlFor="choice">
                  <Field
                    id="choice"
                    name="type"
                    type="radio"
                    value="ESCOLHA"
                    checked={values.type === 'ESCOLHA'}
                  />{' '}
                  <StyledRadio checked={values.type === 'ESCOLHA'}>Mltipla Escolha</StyledRadio>
                </label>
              </div>
              <div className="col-xl-6">
                <label htmlFor="discursive">
                  <Field
                    id="discursive"
                    name="type"
                    type="radio"
                    value="DISCURSIVA"
                    checked={values.type === 'DISCURSIVA'}
                  />{' '}
                  <StyledRadio checked={values.type === 'DISCURSIVA'}>Discursiva</StyledRadio>
                </label>
              </div>
            </div>
            {values.type === 'ESCOLHA' && (
              <div className="">
                <h6>Opes</h6>
                <div>
                  <OptionsInput
                    options={values.options}
                    addOption={this.addOption}
                    updateOptionsArray={this.updateOptionsArray}
                    removeOption={this.removeOption}
                  />
                </div>
              </div>
            )}
            <SaveButton>Salvar</SaveButton>
          </div>

          {mode === 'EDIT' && (
            <div className="col-xl-auto d-flex flex-column">
              <MoveButton className="move">
                <i className="fas fa-bars" />
              </MoveButton>
              <BackButton
                title="Voltar para exibio"
                onClick={(e) => {
                  e.preventDefault();
                  onModeChange('SHOW');
                }}
              >
                <i className="fas fa-backward" />
              </BackButton>
              <DeleteButton>
                <i className="fas fa-times" />
              </DeleteButton>
            </div>
          )}
        </CardFormInfo>
      </Form>
    );
  }
}

const EnhancedForm = withFormik({
  mapPropsToValues: ({ question }) => ({
    questionText: question ? question.questionText : '',
    tip: question ? question.tip : '',
    reason: question ? question.reason : '',
    visible: question ? question.visible : true,
    type: question ? question.type : '',
    options: question ? question.options : [],
  }),
  handleSubmit: (values, { props }) => {
    // based on mode execute custom operation
  },
})(QuestionForm);

export default EnhancedForm;

QuestionForm.propTypes = {
  values: PropTypes.shape({
    questionText: PropTypes.string,
    tip: PropTypes.string,
    reason: PropTypes.string,
    visible: PropTypes.bool,
    type: PropTypes.string,
    options: PropTypes.arrayOf(PropTypes.string),
  }).isRequired,
  setValues: PropTypes.func.isRequired,
  onModeChange: PropTypes.func,
  mode: PropTypes.string.isRequired,
};

QuestionForm.defaultProps = {
  onModeChange: () => {},
};



